.switch {
  width: 42px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-image: linear-gradient(to top, #dcdcdc, #4f2ee8);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  background-color: #fff;
  width: 18px;
  height: 18px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 2px;
}

input:checked + .slider {
  background-image: linear-gradient(to top, #686868, #4f2ee8);
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

.slider.round {
  border-radius: 24px;
}

.slider.round:before {
  border-radius: 50%;
}

.user-acc {
  background: #4f2ee8;
